.footer-container {
  background-color: #1b1b1e;
  width: 100%;
  height: 10vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
}

.copyright {
  color: #6d676e;
  padding-left: 2%;
}

.footer-span {
  color: rgb(185, 185, 185);
  margin-left: 2px;
  text-decoration: none;
}

.footer-link {
  color: rgb(185, 185, 185);
  text-decoration: none;
}

.footer-link:hover {
  color: #faa916;
}

@media (max-width: 450px){
  .footer-container{
    font-size: 0.8em;
    text-align: center;
    -ms-flex-pack: center;
        justify-content: center;
  }
}


