.contact-container {
  padding-top: 10vh;
  min-height: 80vh;
  height: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-image: url("https://d23vnsiihm040t.cloudfront.net/MonitoringBoard+(1)-min.jpeg");
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.contact-card {
  width: auto;
  padding: 2%;
  height: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.contact-form {
  width: 80%;
  margin-left: 10%;
}

@media (max-width: 623px){
  .contact-container{
    padding-bottom: 10vh;
  }
}


